












export default {
	data(){
		return{
			title:"",
			string:''
		}
	},
	onLoad(e) {
		uni.showLoading({
			title: '加载中...',
			mask: false
		});
		console.log(e)
		uni.request({
			url:'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res)
				this.title=res.data.title;
				this.string=res.data.content;
				uni.hideLoading()
			},
			fail: () => {},
			complete: () => {}
		});
	}
}



